@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Banner.css */
.banner {
    background-color: #3498db;
    color: #ffffff;
    padding: 20px;
    text-align: center;
}

.banner h1 {
    font-size: 36px;
    margin-bottom: 10px;
}

.banner p {
    font-size: 18px;
}
/* In your component's stylesheet or a separate CSS file */
.carousel-item {
    margin-top: 50px;
    position: relative;
    text-align: center;
    overflow: hidden;
    padding: 10px;
    /* Add padding for spacing between images */
}

.carousel-item img {
    max-width: 100%;
    max-height: 100%;
    width: auto;
    height: auto;
    vertical-align: middle;
}

.carousel-item:hover img{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    /* Increase image size on hover */
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
    cursor: pointer;
}

.carousel-text {
    position: absolute;
    bottom: 10px;
    /* Adjust vertical position as needed */
    top:30%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    /* background-color: rgba(0, 0, 0, 0.5); */
    /* Semi-transparent background for better text readability */
    color: white;
    /* Text color */
    padding: 5px 10px;
    /* Adjust padding as needed */
    border-radius: 5px;
    /* Rounded corners for the text box */
    font-size: 70px;
    /* Adjust font size as needed */
    display: flex;
    font-weight: 600;;
    flex-direction: column;
    opacity: 0;
    -webkit-animation: fadeIn 4s ease-in-out forwards;
            animation: fadeIn 4s ease-in-out forwards;
    /* Animation for text appearance */
}

.carousel-text span {
    margin: 5px 0;
    /* Adjust spacing between words */
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}
*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

img {
    max-width: 100%;
}

body {
    margin: 0;
    background-color: #ededed;
}

.container {
    background-color: #fff;
    color: #4b4c4d;
    margin: 0;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 1em 2em;

}

.icons-link a {
    color: inherit;
    margin-right: 1em;
    border: 1px solid #ccc;
    font-size: 1.2rem;
    opacity: .75;
    display: inline-block;
    padding: .3em;
    border-radius: 100vw;
}

.icons-link a:hover {
    background-color: #ededed;
}

.icons-link,
ul {
    margin-bottom: 1em;
}

ul a {
    color: inherit;
    margin: .5em;
    font-size: .8rem;
    font-weight: bold;
    text-decoration: none;
}

ul a:hover {
    text-decoration: underline;
}

p {
    color: #aaa;
    font-size: .9rem;
}

@media (min-width: 800px) {
    .container {
        height: 50vh;
    }

    .icons-link a {
        font-size: 2rem;
    }

    .icons-link,
    ul {
        margin-bottom: 2em;
    }

    ul a {
        font-size: 1rem;
    }

    p {
        font-size: 1rem;
    }
}
